import { Col, Row, Table, Typography } from "antd";

const { Title } = Typography;
const CDetailAttachments = ({
  columns,
  data,
  loading,
  dataDisposal,
  loadingDisposal,
  columnsDisposal,
}) => {
  return (
    <Col span={24}>
      <Table
        columns={columns}
        dataSource={data}
        size="small"
        rowKey="id"
        pagination={false}
        loading={loading ? true : false}
      />
      {dataDisposal && dataDisposal.length > 0 && (
        <>
          <Title style={{ marginTop: 30 }} level={4}>
            Disposal
          </Title>
          <Table
            style={{
              marginTop: 10,
            }}
            columns={columnsDisposal}
            dataSource={dataDisposal}
            size="small"
            rowKey="id"
            pagination={false}
            loading={loadingDisposal ? true : false}
          />
        </>
      )}
    </Col>
  );
};

export default CDetailAttachments;
